  .modules__content {
    padding: 30px 30px 10px 30px;

    &.credit--contents {
      display: flex;
      align-items: center;
      padding: 30px 30px 40px;
    }

    .subtitle {
      font-size: 16px;
      color: $text-color;
      margin-bottom: 25px;
    }

    .amounts {
      display: flex;

      ul {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;

        li {
          margin-right: 20px;

          &:last-child {
            margin-right: 0;
          }

          p {
            font-size: 30px;
            line-height: normal;
            cursor: pointer;
            font-weight: 400;
            background: $section-bg;
            padding: 10px 20px;
            text-align: center;
            border-radius: 3px;
            color: $title-color;
            margin: 0;

            &.selected {
              @extend .bg-primary;
              color: #fff;
            }
