    .payment_method {
      li {
        display: inline-block;
        margin-right: 15px;
        margin-bottom: 30px;
      }
    }

    .custom_radio--big {
      label {
        text-align: center;

        img {
          display: block;
          max-width: 192px;
          height: 123px;
          border: 3px solid #e0e3ea;
        }

        span.circle {
          margin-top: 20px;
        }
      }
