.payment_module {
  @extend .cardify;

  .modules__title {
    border-bottom: 1px solid $border-color;
    padding: 30px;
  }

  .payment_tabs {
    padding: 30px 30px 10px;
    position: relative;

    ul.nav-tabs {
      position: absolute;
      display: block;
      left: 30px;
      top: 30px;

      li {
        display: inline-block;
        border: 0;

        a {
          border: 1px solid $border-color;
          padding: 0 40px;
          border-radius: 25px;
          line-height: 50px;
          color: $title-color;

          &.active {
            color: #fff;
            @extend .bg-secondary;
            @extend .bg-secondary;
            position: relative;
            z-index: 1;

            &:before {
              content: none;
            }
          }
        }

        &+li {
          margin-left: -38px;
        }
      }
    }

    .modules__content {
      .cards-logo {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 40px;
        @include xs{
          margin: -5px;
          margin-bottom: 40px;
          flex-wrap: wrap;
          li{
            margin:5px;
          }
        }

        li {
          margin-right: 10px;

          &:last-child {
            margin-right: 0;
          }
        }
      }

      .payment_info {
        .select-wrap select {
          border-radius: 2px;
        }
      }
