/* DASHBOARD EDIT PAGE */
.dashboard-edit {
  .product {
    .product__thumbnail {
      &:before {
        display: none;
      }
    }

    .prod_option {
      position: absolute;
      top: 20px;
      left: 20px;

      &.open {
        .options.dropdown-menu {
          visibility: visible;
          opacity: 1;
        }
      }

      .setting-icon {
        font-size: 20px;
        line-height: 45px;
        width: 45px;
        text-align: center;
        background: #000;
        color: #fff;
        display: inline-block;
        @extend .bg-primary;
        -webkit-border-radius: 200px;
        -moz-border-radius: 200px;
        border-radius: 200px;
        cursor: pointer;
      }

      .options {
        position: absolute;
        width: 120px;
        background: #fff;
        padding: 20px;
        border-radius: 4px;
        opacity: 0;
        display: initial;
        transition: $transition;
        visibility: hidden;
        top: calc(100% + 20px);

        &:before {
          content: '';
          position: absolute;
          bottom: 100%;
          left: 10px;
          border-bottom: 10px solid #fff;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
        }

        ul {
          li {
            a {
              color: #54667a;
              padding: 5px 0;
              transition: $transition;
              display: inline-block;

              span {
                margin-right: 5px;
                font-size: 14px;
              }

              &:hover {
                @extend .color-primary;
              }

              &.delete:hover {
                @extend .color-danger;
              }
            }
          }
        }
      }
    }
  }
}

