      line-height: 28px;
      font-weight: 400;

      a {
        @extend .color-primary;
      }
    }
  }

  .option-single {
    padding-bottom: 30px;
    border-bottom: 1px solid $border-color;
    margin-bottom: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    position: relative;

    &:last-child {
      border-bottom: 0;
      margin-bottom: 0;
    }

    .custom-radio {
      margin-bottom: 0;
    }

    .op_action {
      display: flex;
      align-items: center;

      .exp_date {
        padding-right: 45px;

        &.expired {
          color: $red;
        }
      }

      .ac_btn {
        position: absolute;
        right: 0;
        top: 5px;

        .dropdown-menu {
          left: auto !important;
          right: 0;
          border-radius: 0;
          box-shadow: 0 3px 15px rgba(0, 0, 0, 0.13);
          border: 0 none;
          padding: 15px 0;

          .dropdown-item {
            &:hover {
              @extend .color-primary;
              background: none;
            }
          }
        }

        .dropdown-toggle {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          transition: $transition-base;

          &:hover,
          &:focus {
            @extend .bg-primary;

            .svg {
              fill: #fff;
            }
          }
        }

        .dropdown-toggle {
          &:after {
            content: none;
          }

          span {
            width: 18px;
            display: block;
            margin-right: -3px;

            .svg {
              fill: $text-color;
              transition: $transition-base;
              width: 14px;
            }
          }
        }
      }
    }
  }

  .custom-radio {
    margin-bottom: 10px;

    label {
      color: #333;

      .card-name {
        margin-left: 20px;
